function suCallback(name, data) {
  if (window.hasOwnProperty('sketchup') && window.sketchup) {
    if (window.sketchup.hasOwnProperty(name)) {
      window.sketchup[name](data);
      return null;
    }
  } else {
    if (data !== undefined) {
      // null, object, array, number, string, function, boolean
      data = JSON.stringify(data); // can return undefined as well; handled below
    }
    if (data === undefined) {
      data = '';
    }
    // window.location.href = 'skp:' + name + '@' + data;
    return null;
  }
  console.log(`SketchUp callback function, "${name}", does not exist!`);
}

export default suCallback;
