import React, { Component } from 'react';
import { connect } from 'react-redux';
import styles from '../styles/PieMenuContext.module.css';

class PieMenuContext extends Component {
  render() {
    const {
      activeCategoryIndex,
      allAvailableCommands,
      categories,
    } = this.props;
    if (activeCategoryIndex === null) {
      return null;
    }
    return <div className={styles.wrapper}></div>;
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    activeCategoryIndex: main.activeCategoryIndex,
    allAvailableCommands: main.allAvailableCommands,
    categories: main.categories,
  };
}

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(PieMenuContext);
