import React, { Component } from 'react';
import { connect } from 'react-redux';
import styles from '../styles/Context.module.css';
import PieMenuContext from './PieMenuContext';
import ManageIconCategoryContext from './ManageIconCategoryContext';
import IconMenuSettingsContext from './IconMenuSettingsContext';

class Context extends Component {
  render() {
    const { mode } = this.props;
    let toRender = null;
    if (mode === 0) {
      toRender = <PieMenuContext />;
    } else if (mode === 1) {
      toRender = <ManageIconCategoryContext />;
    } else if (mode === 2) {
      toRender = <IconMenuSettingsContext />;
    }

    return <div className={styles.wrapper}>{toRender}</div>;
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    mode: main.mode,
  };
}

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Context);
