import React, { useRef } from 'react';
import { useDrag, useDrop } from 'react-dnd';
import styles from '../styles/CategoryCard.module.css';

function CategoryCard({ card, moveCard }) {
  const ref = useRef(null);

  const [collected, connectDrag] = useDrag(() => ({
    item: { type: 'categoryCard', id: card.id },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  }));

  const [{ handlerId }, connectDrop] = useDrop({
    accept: 'categoryCard',
    collect(monitor) {
      return {
        handlerId: monitor.getHandlerId(),
      };
    },
    hover(hoveredOverItem) {
      if (hoveredOverItem.id !== card.id) {
        moveCard(hoveredOverItem.id, card.id);
      }
    },
  });

  connectDrag(ref);
  connectDrop(ref);

  return (
    <div
      ref={ref}
      className={
        styles.itemWrapper +
        (collected.isDragging ? ' ' + styles.itemDragging : '')
      }
      data-handler-id={handlerId}
    >
      <div className={styles.item}>
        <p className={styles.text}>{card.title}</p>
      </div>
    </div>
  );
}

export default CategoryCard;
