import React, { useRef, useCallback } from 'react';
import { useDrop } from 'react-dnd';
import { connect } from 'react-redux';
import styles from '../styles/CardContainer.module.css';
import {
  setAllAvailableCommands,
  setActiveCategoryCommandIds,
} from '../actions/main';
import RightCard from './RightCard';

function RightCardContainer({
  categories,
  activeCategoryIndex,
  allAvailableCommands,
  setAllAvailableCommands,
  setActiveCategoryCommandIds,
}) {
  const ref = useRef(null);

  let activeCategory = null;
  if (activeCategoryIndex !== null) {
    activeCategory = categories[activeCategoryIndex];
  }

  const [rightCardSate, connectRightCardDrop] = useDrop(() => ({
    // The type (or types) to accept - strings or symbols
    accept: 'rightCard',
    // Props to collect
    collect: (monitor) => ({
      isOver: monitor.isOver(),
      canDrop: monitor.canDrop(),
    }),
    drop(droppedItem) {},
  }));

  const [leftCardSate, connectLeftCardDrop] = useDrop(
    () => ({
      // The type (or types) to accept - strings or symbols
      accept: 'leftCard',
      // Props to collect
      collect: (monitor) => ({
        isOver: monitor.isOver(),
        canDrop: monitor.canDrop(),
      }),
      drop(droppedItem) {
        if (activeCategory) {
          setActiveCategoryCommandIds(
            activeCategory.commandIds.filter((id) => id !== droppedItem.id)
          );
        }
      },
    }),
    [setActiveCategoryCommandIds, activeCategory]
  );

  const handleMoveCard = useCallback(
    (sourceId, destinationId) => {
      let sourceIndex = null;
      let destinationIndex = null;
      const numAvailItems = allAvailableCommands.length;
      for (let i = 0; i < numAvailItems; ++i) {
        const id = allAvailableCommands[i].id;
        if (id === sourceId) {
          sourceIndex = i;
        } else if (id === destinationId) {
          destinationIndex = i;
        }
      }
      if (sourceIndex === null || destinationIndex === null) {
        return;
      }

      const newCommands = [...allAvailableCommands];
      const dragCard = newCommands[sourceIndex];
      newCommands.splice(sourceIndex, 1);
      newCommands.splice(destinationIndex, 0, dragCard);
      setAllAvailableCommands(newCommands);
    },
    [allAvailableCommands, setAllAvailableCommands]
  );

  connectRightCardDrop(ref);
  connectLeftCardDrop(ref);

  if (!activeCategory) {
    return null;
  }

  return (
    <div
      ref={ref}
      className={
        styles.wrapper +
        ' ' +
        (rightCardSate.isOver || leftCardSate.isOver
          ? styles.cardOver
          : styles.cardNotOver)
      }
    >
      {allAvailableCommands.map((c, i) => {
        if (activeCategory.commandIds.includes(c.id)) {
          return null;
        }
        return <RightCard key={c.id} card={c} moveCard={handleMoveCard} />;
      })}
    </div>
  );
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    categories: main.categories,
    activeCategoryIndex: main.activeCategoryIndex,
    allAvailableCommands: main.allAvailableCommands,
  };
}

const mapDispatchToProps = {
  setAllAvailableCommands,
  setActiveCategoryCommandIds,
};

export default connect(mapStateToProps, mapDispatchToProps)(RightCardContainer);
