import store from '../store';

import suCallback from './suCallback';
import {
  setMode,
  setCategories,
  setActiveCategoryIndex,
  setAllAvailableCommands,
  setActiveCategoryCommandIds,
} from '../actions/main';

function createBridge() {
  window.suBridge = {
    setMode: (page) => store.dispatch(setMode(page)),
    setCategories: (categories) =>
      store.dispatch(setCategories(categories, false)),
    setActiveCategoryIndex: (index) =>
      store.dispatch(setActiveCategoryIndex(index, false)),
    setAllAvailableCommands: (index) =>
      store.dispatch(setAllAvailableCommands(index)),
    setActiveCategoryCommandIds: (index) =>
      store.dispatch(setActiveCategoryCommandIds(index, false)),
  };

  window.addEventListener('error', function (event) {
    try {
      suCallback('onError', event.message);
    } catch (err) {
      console.log(err);
    }
  });
}

export default createBridge;
