import React, { Component } from 'react';
import { connect } from 'react-redux';
import styles from '../styles/ManageIconCategoryToolbar.module.css';
import {
  setMode,
  setCategoryTitle,
  setActiveCategoryIndex,
} from '../actions/main';
import NextCategoryIcon from '../assets/pie-menu-next.svg';
import PrevCategoryIcon from '../assets/pie-menu-prev.svg';
import Button from './Button';

class ManageIconCategoryToolbar extends Component {
  constructor(props) {
    super(props);
    this.handleNextClick = this.handleNextClick.bind(this);
    this.handlePrevClick = this.handlePrevClick.bind(this);
    this.handleTextInputChange = this.handleTextInputChange.bind(this);
    this.handleTextInputBlur = this.handleTextInputBlur.bind(this);

    this.state = {
      text: '',
    };
  }

  handleTextInputChange(e) {
    this.setState({ text: e.target.value });
  }

  handleTextInputBlur(e) {
    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      return;
    }

    this.props.setCategoryTitle(activeCategoryIndex, this.state.text.trim());
  }

  componentDidMount() {
    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      this.setState({ text: '' });
      return;
    }
    const activeCategory = categories[activeCategoryIndex];
    if (!activeCategory) {
      this.setState({ text: '' });
      return;
    }
    this.setState({ text: activeCategory.title });
  }

  componentDidUpdate(prevProps) {
    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      this.setState({ text: '' });
      return;
    }
    const activeCategory = categories[activeCategoryIndex];
    if (!activeCategory) {
      this.setState({ text: '' });
      return;
    }
    if (
      prevProps.categories !== categories ||
      prevProps.activeCategoryIndex !== activeCategoryIndex
    ) {
      this.setState({ text: activeCategory.title });
    }
  }

  handlePrevClick(e) {
    e.preventDefault();

    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      return;
    }

    if (activeCategoryIndex > 0) {
      this.props.setActiveCategoryIndex(activeCategoryIndex - 1);
    }
  }

  handleNextClick(e) {
    e.preventDefault();

    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      return;
    }

    if (activeCategoryIndex + 1 < categories.length) {
      this.props.setActiveCategoryIndex(activeCategoryIndex + 1);
    }
  }

  render() {
    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      return null;
    }
    const activeCategory = categories[activeCategoryIndex];
    if (!activeCategory) {
      return null;
    }

    return (
      <div className={styles.wrapper}>
        <input
          type="text"
          value={this.state.text}
          onChange={this.handleTextInputChange}
          onBlur={this.handleTextInputBlur}
          className={styles.textInput}
        />
        <div className={styles.iconWrapper} onClick={this.handlePrevClick}>
          <img
            src={PrevCategoryIcon}
            className={styles.icon}
            alt="previous category"
          />
        </div>
        <div className={styles.iconWrapper} onClick={this.handleNextClick}>
          <img
            src={NextCategoryIcon}
            className={styles.icon}
            alt="next category"
          />
        </div>
        <Button
          onClick={(e) => {
            e.preventDefault();
            this.props.setMode(0);
          }}
          color="sea"
          small={true}
          className={styles.goBack}
        >Go Back</Button>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    categories: main.categories,
    activeCategoryIndex: main.activeCategoryIndex,
  };
}

const mapDispatchToProps = {
  setMode,
  setCategoryTitle,
  setActiveCategoryIndex,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ManageIconCategoryToolbar);
