import { SET_CONFIRMATION_MODAL } from './types';

export function showConfirmationModal({ title, message, onSubmit, onCancel }) {
  return {
    type: SET_CONFIRMATION_MODAL,
    payload: { title, message, onSubmit, onCancel },
  };
}

export function hideConfirmationModal() {
  return {
    type: SET_CONFIRMATION_MODAL,
    payload: null,
  };
}
