import React, { Component } from 'react';
import { connect, batch } from 'react-redux';
import styles from '../styles/Main.module.css';
import Toolbar from './Toolbar';
import Context from './Context';
import ConfirmationModal from './ConfirmationModal';
import {
  setCategories,
  setActiveCategoryIndex,
  setAllAvailableCommands,
} from '../actions/main';
import {
  TEST_CATEGORIES,
  TEST_ACTIVE_CATEGORY_INDEX,
  TEST_ALL_AVAILABLE_COMMANDS,
} from '../utils/constants';

class Main extends Component {
  componentDidMount() {
    batch(() => {
      this.props.setCategories(TEST_CATEGORIES);
      this.props.setActiveCategoryIndex(TEST_ACTIVE_CATEGORY_INDEX);
      this.props.setAllAvailableCommands(TEST_ALL_AVAILABLE_COMMANDS);
    });
  }

  render() {
    return (
      <div className={styles.wrapper}>
        <Toolbar />
        <Context />
        <ConfirmationModal />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {};
}

const mapDispatchToProps = {
  setCategories,
  setActiveCategoryIndex,
  setAllAvailableCommands,
};

export default connect(mapStateToProps, mapDispatchToProps)(Main);
