import React, { Component } from 'react';
import { batch, connect } from 'react-redux';
import styles from '../styles/PieMenuToolbar.module.css';
import {
  setMode,
  addCategory,
  removeCategory,
  setActiveCategoryIndex,
} from '../actions/main';
import { showConfirmationModal } from '../actions/modal';
import AddCategoryIcon from '../assets/pm-add-icon.svg';
import RemoveCategoryIcon from '../assets/pm-remove-icon.svg';
import SettingsIcon from '../assets/pm-settings-icon.svg';
import Button from './Button';

class PieMenuToolbar extends Component {
  constructor(props) {
    super(props);
    this.handleAddClick = this.handleAddClick.bind(this);
    this.handleRemoveClick = this.handleRemoveClick.bind(this);
    this.handleSettingsClick = this.handleSettingsClick.bind(this);
  }

  handleAddClick(e) {
    e.preventDefault();

    const newIndex = this.props.categories.length;
    batch(() => {
      this.props.addCategory({
        title: `Category ${newIndex + 1}`,
        commandIds: [],
      });
      this.props.setActiveCategoryIndex(newIndex);
      this.props.setMode(1);
    });
  }

  handleRemoveClick(e) {
    e.preventDefault();

    const { categories, activeCategoryIndex } = this.props;
    if (categories.length === 0 || activeCategoryIndex === null) {
      return;
    }

    const activeCategory = categories[activeCategoryIndex];

    this.props.showConfirmationModal({
      title: 'Are you sure?',
      message: `Are you sure you want to delete the category, '${activeCategory.title}'?`,
      onSubmit: (close) => {
        let newIndex = activeCategoryIndex;
        let lastIndex = categories.length - 2;
        if (categories.length <= 1) {
          newIndex = null;
        } else if (newIndex > lastIndex) {
          newIndex = lastIndex;
        }
        batch(() => {
          this.props.removeCategory(activeCategoryIndex);
          this.props.setActiveCategoryIndex(newIndex);
        });
        close();
      },
    });
  }

  handleSettingsClick(e) {
    e.preventDefault();

    this.props.setMode(2);
  }

  render() {
    const { categories, activeCategoryIndex } = this.props;
    let activeCategory = null;
    if (categories.length > 0 && activeCategoryIndex !== null) {
      activeCategory = categories[activeCategoryIndex];
    }

    return (
      <div className={styles.wrapper}>
        <p className={styles.title}>
          {activeCategory ? activeCategory.title : ''}
        </p>
        <Button
          onClick={(e) => {
            e.preventDefault();
            this.props.setMode(1);
          }}
          color="sea"
          small={true}
        >
          Edit
        </Button>
        <div className={styles.iconWrapper} onClick={this.handleAddClick}>
          <img
            src={AddCategoryIcon}
            className={styles.icon}
            alt="add category"
          />
        </div>
        <div
          className={styles.iconWrapper}
          onClick={this.handleRemoveClick}
          disabled={activeCategory ? false : true}
        >
          <img
            src={RemoveCategoryIcon}
            className={styles.icon}
            alt="remove category"
          />
        </div>
        <div className={styles.iconWrapper} onClick={this.handleSettingsClick}>
          <img src={SettingsIcon} className={styles.icon} alt="settings" />
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    categories: main.categories,
    activeCategoryIndex: main.activeCategoryIndex,
  };
}

const mapDispatchToProps = {
  setMode,
  showConfirmationModal,
  addCategory,
  removeCategory,
  setActiveCategoryIndex,
};

export default connect(mapStateToProps, mapDispatchToProps)(PieMenuToolbar);
