import React, { Component } from 'react';
import { connect } from 'react-redux';
import styles from '../styles/Toolbar.module.css';
import PieMenuToolbar from './PieMenuToolbar';
import ManageIconCategoryToolbar from './ManageIconCategoryToolbar';
import IconMenuSettingsToolbar from './IconMenuSettingsToolbar';

class Toolbar extends Component {
  render() {
    const { mode } = this.props;
    let toRender = null;
    if (mode === 0) {
      toRender = <PieMenuToolbar />;
    } else if (mode === 1) {
      toRender = <ManageIconCategoryToolbar />;
    } else if (mode === 2) {
      toRender = <IconMenuSettingsToolbar />;
    }

    return <div className={styles.wrapper}>{toRender}</div>;
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    mode: main.mode,
  };
}

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Toolbar);
