import React, { useRef } from 'react';
import { useDrag, useDrop } from 'react-dnd';
import styles from '../styles/Card.module.css';

function LeftCard({ card, moveCard }) {
  const ref = useRef(null);

  const [collected, connectDrag] = useDrag(() => ({
    item: { type: 'leftCard', id: card.id },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  }));

  const [{ handlerId }, connectDrop] = useDrop({
    accept: 'leftCard',
    collect(monitor) {
      return {
        handlerId: monitor.getHandlerId(),
      };
    },
    hover(hoveredOverItem) {
      if (hoveredOverItem.id !== card.id) {
        moveCard(hoveredOverItem.id, card.id);
      }
    },
  });

  connectDrag(ref);
  connectDrop(ref);

  return (
    <div
      ref={ref}
      className={styles.itemWrapper}
      style={{
        opacity: collected.isDragging ? 0.5 : 1,
        backgroundColor: card.backgroundColor,
      }}
      // data-handler-id={handlerId}
    >
      {/* The drag ref marks this node as being the "pick-up" node */}
      <div
        // ref={ref}
        className={styles.item}
        style={{ backgroundColor: card.backgroundColor }}
      >
        {card.id}
      </div>
    </div>
  );
}

export default LeftCard;
