import React, { Component } from 'react';
import { connect } from 'react-redux';
import styles from '../styles/IconMenuSettingsToolbar.module.css';
import Button from './Button';
import { setMode } from '../actions/main';

class IconMenuSettingsToolbar extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className={styles.wrapper}>
        <Button
          onClick={(e) => {
            e.preventDefault();
            this.props.setMode(0);
          }}
          color="sea"
          small={true}
          className={styles.goBack}
        >
          Go Back
        </Button>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return {};
}

const mapDispatchToProps = {
  setMode,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(IconMenuSettingsToolbar);
