import React, { Component } from 'react';
import { connect } from 'react-redux';
import styles from '../styles/ManageIconCategoryContext.module.css';
import { setAllAvailableCommands } from '../actions/main';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import LeftCardContainer2 from './LeftCardContainer';
import RightCardContainer2 from './RightCardContainer';

class ManageIconCategoryContext extends Component {
  constructor(props) {
    super(props);
    this.handleMoveCard = this.handleMoveCard.bind(this);

    this.leftContainerRef = React.createRef();
    this.rightContainerRef = React.createRef();
  }

  handleMoveCard(sourceId, destinationId) {
    const { allAvailableCommands } = this.props;
    let sourceIndex = null;
    let destinationIndex = null;
    const numAvailItems = allAvailableCommands.length;
    for (let i = 0; i < numAvailItems; ++i) {
      const id = allAvailableCommands[i].id;
      if (id === sourceId) {
        sourceIndex = i;
      } else if (id === destinationId) {
        destinationIndex = i;
      }
    }
    if (sourceIndex === null || destinationIndex === null) {
      return;
    }

    const newCommands = [...allAvailableCommands];
    const dragCard = newCommands[sourceIndex];
    newCommands.splice(sourceIndex, 1);
    newCommands.splice(destinationIndex, 0, dragCard);
    this.props.setAllAvailableCommands(newCommands);
  }

  render() {
    return (
      <div className={styles.box}>
        <DndProvider backend={HTML5Backend}>
          <div className={styles.outerContainer + ' ' + styles.outerLeft}>
            <LeftCardContainer2 />
          </div>
          <div className={styles.outerContainer + ' ' + styles.outerRight}>
            <RightCardContainer2 />
          </div>
        </DndProvider>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return { allAvailableCommands: main.allAvailableCommands };
}

const mapDispatchToProps = {
  setAllAvailableCommands,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ManageIconCategoryContext);
