import React, { useRef, useCallback } from 'react';
import { useDrop } from 'react-dnd';
import { connect } from 'react-redux';
import styles from '../styles/CategoryOrderingContainer.module.css';
import { setCategories, setActiveCategoryIndex } from '../actions/main';
import CategoryCard from './CategoryCard';

function CategoryOrderingContainer({
  categories,
  activeCategoryIndex,
  setCategories,
  setActiveCategoryIndex,
}) {
  const ref = useRef(null);

  const [categoryCardState, connectCategoryCardDrop] = useDrop(() => ({
    // The type (or types) to accept - strings or symbols
    accept: 'categoryCard',
    // Props to collect
    collect: (monitor) => ({
      isOver: monitor.isOver(),
      canDrop: monitor.canDrop(),
    }),
    drop(droppedItem) {},
  }));

  const handleMoveCard = useCallback(
    (sourceId, destinationId) => {
      let sourceIndex = null;
      let destinationIndex = null;
      const numAvailItems = categories.length;
      for (let i = 0; i < numAvailItems; ++i) {
        const id = categories[i].id;
        if (id === sourceId) {
          sourceIndex = i;
        } else if (id === destinationId) {
          destinationIndex = i;
        }
      }
      if (sourceIndex === null || destinationIndex === null) {
        return;
      }

      const newCategories = [...categories];
      const dragCard = newCategories[sourceIndex];
      newCategories.splice(sourceIndex, 1);
      newCategories.splice(destinationIndex, 0, dragCard);
      setCategories(newCategories);
    },
    [categories, activeCategoryIndex, setCategories, setActiveCategoryIndex]
  );

  connectCategoryCardDrop(ref);
  return (
    <div
      ref={ref}
      className={
        styles.wrapper +
        ' ' +
        (categoryCardState.isOver ? styles.cardOver : styles.cardNotOver)
      }
    >
      {categories.map((c, i) => {
        return <CategoryCard key={c.id} card={c} moveCard={handleMoveCard} />;
      })}
    </div>
  );
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    categories: main.categories,
    activeCategoryIndex: main.activeCategoryIndex,
  };
}

const mapDispatchToProps = {
  setCategories,
  setActiveCategoryIndex,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CategoryOrderingContainer);
