import React from 'react';
import styles from '../styles/Checkbox.module.css';

function Checkbox({
  checked,
  children,
  useRadio,
  onChange,
  containerClass,
  checkmarkClass,
  disabled,
  id,
}) {
  let containerClass2 = styles.container;
  if (containerClass) {
    containerClass2 += ' ' + containerClass;
  }
  let checkmarkClass2 = styles.checkmark;
  if (checkmarkClass) {
    checkmarkClass2 += ' ' + checkmarkClass;
  }

  return (
    <label className={containerClass2}>
      <input
        type={useRadio ? 'radio' : 'checkbox'}
        onChange={onChange}
        checked={checked}
        disabled={disabled ? true : false}
        id={id}
      />
      <span className={checkmarkClass2}></span>
      <div className={styles.children}>{children}</div>
    </label>
  );
}

export default Checkbox;
