import React, { useRef, useCallback } from 'react';
import { useDrop } from 'react-dnd';
import { connect } from 'react-redux';
import styles from '../styles/CardContainer.module.css';
import { setActiveCategoryCommandIds } from '../actions/main';
import LeftCard from './LeftCard';

function LeftCardContainer({
  categories,
  allAvailableCommands,
  setActiveCategoryCommandIds,
  activeCategoryIndex,
}) {
  const ref = useRef(null);

  let activeCategory = null;
  if (activeCategoryIndex !== null) {
    activeCategory = categories[activeCategoryIndex];
  }

  const [rightCardState, connectRightCardDrop] = useDrop(
    () => ({
      // The type (or types) to accept - strings or symbols
      accept: 'rightCard',
      // Props to collect
      collect: (monitor) => ({
        isOver: monitor.isOver(),
        canDrop: monitor.canDrop(),
      }),
      drop(droppedItem) {
        if (
          activeCategory &&
          !activeCategory.commandIds.includes(droppedItem.id)
        ) {
          setActiveCategoryCommandIds([
            ...activeCategory.commandIds,
            droppedItem.id,
          ]);
        }
      },
    }),
    [activeCategory, setActiveCategoryCommandIds]
  );

  const handleMoveCard = useCallback(
    (sourceId, destinationId) => {
      let sourceIndex = null;
      let destinationIndex = null;
      const numAvailItems = activeCategory.commandIds.length;
      for (let i = 0; i < numAvailItems; ++i) {
        const id = activeCategory.commandIds[i];
        if (id === sourceId) {
          sourceIndex = i;
        } else if (id === destinationId) {
          destinationIndex = i;
        }
      }
      if (sourceIndex === null || destinationIndex === null) {
        return;
      }

      const newCommands = [...activeCategory.commandIds];
      const dragCard = newCommands[sourceIndex];
      newCommands.splice(sourceIndex, 1);
      newCommands.splice(destinationIndex, 0, dragCard);
      setActiveCategoryCommandIds(newCommands);
    },
    [activeCategory, setActiveCategoryCommandIds]
  );

  connectRightCardDrop(ref);

  if (!activeCategory) {
    return null;
  }

  return (
    <div
      ref={ref}
      className={
        styles.wrapper +
        ' ' +
        (rightCardState.isOver ? styles.cardOver : styles.cardNotOver)
      }
    >
      {activeCategory.commandIds.map((id, i) => {
        let c = null;
        for (let d of allAvailableCommands) {
          if (d.id === id) {
            c = d;
            break;
          }
        }
        if (!c) {
          return null;
        }
        return <LeftCard key={c.id} card={c} moveCard={handleMoveCard} />;
      })}
    </div>
  );
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    categories: main.categories,
    activeCategoryIndex: main.activeCategoryIndex,
    allAvailableCommands: main.allAvailableCommands,
  };
}

const mapDispatchToProps = {
  setActiveCategoryCommandIds,
};

export default connect(mapStateToProps, mapDispatchToProps)(LeftCardContainer);
