import React, { Component } from 'react';
import { connect } from 'react-redux';
import styles from '../styles/ConfirmationModal.module.css';
import Button from './Button';
import { hideConfirmationModal } from '../actions/modal';

class ConfirmationModal extends Component {
  constructor(props) {
    super(props);

    this.handleSubmitClick = this.handleSubmitClick.bind(this);
    this.handleCancelClick = this.handleCancelClick.bind(this);
  }

  handleSubmitClick(e) {
    e.preventDefault();
    const { confirmation } = this.props;
    if (confirmation && confirmation.onSubmit) {
      confirmation.onSubmit(this.props.hideConfirmationModal);
    } else {
      this.props.hideConfirmationModal();
    }
  }

  handleCancelClick(e) {
    e.preventDefault();
    const { confirmation } = this.props;
    if (confirmation && confirmation.onCancel) {
      confirmation.onCancel(this.props.hideConfirmationModal);
    } else {
      this.props.hideConfirmationModal();
    }
  }

  render() {
    const { confirmation } = this.props;

    if (!confirmation) {
      return null;
    }

    return (
      <div className={styles.wrapper}>
        <div className={styles.box}>
          <p className={styles.title}>{confirmation.title}</p>
          <p className={styles.message}>{confirmation.message}</p>
          <div className={styles.buttonsContainer}>
            <Button onClick={this.handleSubmitClick} color="sea">
              Yes
            </Button>
            <Button onClick={this.handleCancelClick} color="grey">
              No
            </Button>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { modal } = state;
  return { confirmation: modal.confirmation };
}

const mapDispatchToProps = {
  hideConfirmationModal,
};

export default connect(mapStateToProps, mapDispatchToProps)(ConfirmationModal);
