import {
  ADD_CATEGORY,
  REMOVE_CATEGORY,
  SET_ACTIVE_CATEGORY_INDEX,
  SET_CATEGORIES,
  SET_MODE,
  SET_CATEGORY_TITLE,
  SET_ALL_AVAILABLE_COMMANDS,
  SET_ACTIVE_CATEGORY_COMMAND_IDS,
} from './types';
import suCallback from '../utils/suCallback';

export function setMode(payload) {
  return {
    type: SET_MODE,
    payload,
  };
}

export function setCategories(payload, triggerCallback = true) {
  if (triggerCallback) {
    suCallback('setCategories', payload);
  }
  return {
    type: SET_CATEGORIES,
    payload,
  };
}

export function setActiveCategoryIndex(payload, triggerCallback = true) {
  if (triggerCallback) {
    suCallback('setActiveCategoryIndex', payload);
  }
  return {
    type: SET_ACTIVE_CATEGORY_INDEX,
    payload,
  };
}

export const addCategory = (payload, triggerCallback = true) => (
  dispatch,
  getState
) => {
  dispatch({
    type: ADD_CATEGORY,
    payload,
  });
  if (triggerCallback) {
    suCallback('setCategories', getState().main.categories);
  }
};

export const removeCategory = (index, triggerCallback = true) => (
  dispatch,
  getState
) => {
  dispatch({
    type: REMOVE_CATEGORY,
    payload: index,
  });
  if (triggerCallback) {
    suCallback('setCategories', getState().main.categories);
  }
};

export const setCategoryTitle = (index, title, triggerCallback = true) => (
  dispatch,
  getState
) => {
  dispatch({
    type: SET_CATEGORY_TITLE,
    payload: { index, title },
  });
  if (triggerCallback) {
    suCallback('setCategories', getState().main.categories);
  }
};

export function setAllAvailableCommands(payload) {
  return {
    type: SET_ALL_AVAILABLE_COMMANDS,
    payload,
  };
}

export const setActiveCategoryCommandIds = (
  payload,
  triggerCallback = true
) => (dispatch, getState) => {
  dispatch({
    type: SET_ACTIVE_CATEGORY_COMMAND_IDS,
    payload,
  });
  if (triggerCallback) {
    suCallback('setCategories', getState().main.categories);
  }
};
