import { SET_CONFIRMATION_MODAL } from '../actions/types';

const initialState = {
  confirmation: null,
};

function modalReducer(state = initialState, action) {
  const { payload } = action;
  switch (action.type) {
    case SET_CONFIRMATION_MODAL:
      return {
        ...state,
        confirmation: payload,
      };
    default:
      return state;
  }
}

export default modalReducer;
