import React, { Component } from 'react';
import styles from '../styles/Button.module.css';

class Button extends Component {
  render() {
    const { color, children, onClick, disabled, className, small } = this.props;
    let additionalStyles = '';
    if (color === 'sea') {
      additionalStyles = ' ' + styles.sea;
    } else {
      additionalStyles = ' ' + styles.grey;
    }
    if (small) {
      additionalStyles += ' ' + styles.small;
    }
    if (className) {
      additionalStyles += ' ' + className;
    }

    return (
      <button
        className={styles.button + additionalStyles}
        type="button"
        onClick={onClick}
        disabled={disabled}
      >
        {children}
      </button>
    );
  }
}

export default Button;
